html {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(#B363C4), to(#6157D1));
  background: linear-gradient(90deg, #B363C4, #6157D1);
  background-color: #B363C4;
}

.container {
  padding-right: 25px;
  padding-left: 25px;
  margin-right: auto;
  margin-left: auto;
}

.todo .container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #FEF7FF;
  padding: 0 20px;
  padding-bottom: 50px;
  font-size: 3rem;
}

.todo h2 {
  font-size: 4rem;
  font-weight: 500;
  margin-top: 30px;
}

.input-icon {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 350px;
}

.input-icon img {
  position: absolute;
  right: 10px;
  color: #B363C4;
  cursor: pointer;
  height: 2.5rem;
  top: 50%;
  transform: translatey(-50%);
  -webkit-transform: translatey(-50%);
  -moz-transform: translatey(-50%);
  -ms-transform: translatey(-50%);
  -o-transform: translatey(-50%);
}

.input-icon input {
  display: block;
  width: 100%;
  height: 2.5rem;
  outline: none;
  border: 2px solid white;
  padding-left: 15px;
  font-size: 1.6rem;
}

.the-list {
  margin-top: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  width: 100%;
  max-width: 700px;
}

.task {
  -webkit-animation: openTask .5s ease;
          animation: openTask .5s ease;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row;
          flex-flow: row;
  background-color: white;
  height: 3rem;
  padding: 10px;
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: black;
  -webkit-transition: .5s;
  transition: .5s;
}

@-webkit-keyframes done {
  50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    min-width: 40%;
  }
}

@keyframes done {
  50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    min-width: 40%;
  }
}

.task h3 {
  font-weight: 500;
}

.done {
  opacity: .6 !important;
  -webkit-transform: scale(0.9) !important;
          transform: scale(0.9) !important;
}

.task img:nth-child(1) {
  color: white;
  height: 2.5rem;
  border-radius: 10px;
  cursor: pointer;
}

.task img:nth-child(2) {
  margin-left: 10px;
  color: white;
  height: 1.4rem;
  padding: .5rem;
  border-radius: 10px;
  background-color: #EB4522;
  cursor: pointer;
}

.done h3 {
  text-decoration: line-through;
}

.delet {
  -webkit-transition: .5s;
  transition: .5s;
  -webkit-transform: scale(0);
          transform: scale(0);
}

@media (max-width: 600px) {
  html {
    font-size: 12px;
  }
  .input-icon {
    width: 90%;
  }
  .the-list .task {
    width: 70%;
  }
}
